@use "../config/g_prefix" as g;
@use "../config/table" as table;

@each $key, $value in table.$table-layout {
    .#{g.$prefix-cls}-#{$key} {
        table-layout: $value;
    }
}

@each $key, $value in table.$caption-side {
    .#{g.$prefix-cls}-#{$key} {
        caption-side: $value;
    }
}

@each $key, $value in table.$border-collapse {
    .#{g.$prefix-cls}-#{$key} {
        border-collapse: $value;
    }
}

@each $key, $value in table.$border-spacing {
    .#{g.$prefix-cls}-#{$key} {
        border-spacing: $value;
    }
}

@each $key, $value in table.$empty-cells {
    .#{g.$prefix-cls}-#{$key} {
        empty-cells: $value;
    }
}