@use "../config/blend" as blend;
@use "../config/g_prefix" as g;

@each $key, $value in blend.$mix-blend {
    .#{g.$prefix-cls}-#{$key} {
        mix-blend-mode: #{$value};
    }
}

@each $key, $value in blend.$bg-blend {
    .#{g.$prefix-cls}-#{$key} {
        background-blend-mode: #{$value};
    }
}