@use "../config/borders" as borders;
@use "../config/g_prefix" as g;

@each $key, $value in borders.$borders-width {
    .#{g.$prefix-cls}-ba-#{$key} {
        border: $value;
    }

    .#{g.$prefix-cls}-bt-#{$key} {
        border-top: $value;
    }

    .#{g.$prefix-cls}-br-#{$key} {
        border-right: $value;
    }

    .#{g.$prefix-cls}-bb-#{$key} {
        border-bottom: $value;
    }

    .#{g.$prefix-cls}-bl-#{$key} {
        border-left: $value;
    }
}

@each $key, $value in borders.$borders-radius {
    .#{g.$prefix-cls}-br#{$key} {
        border-radius: $value;
    }

    .#{g.$prefix-cls}-br-top-#{$key} {
        border-top-left-radius: $value;
        border-top-right-radius: $value;
    }

    .#{g.$prefix-cls}-br-right-#{$key} {
        border-top-right-radius: $value;
        border-bottom-right-radius: $value;
    }

    .#{g.$prefix-cls}-br-bottom-#{$key} {
        border-bottom-left-radius: $value;
        border-bottom-right-radius: $value;
    }

    .#{g.$prefix-cls}-br-left-#{$key} {
        border-top-left-radius: $value;
        border-bottom-left-radius: $value;
    }
}

@each $key, $value in borders.$border-styles {
    .#{g.$prefix-cls}-ba-#{$value} {
        border-style: $value;
    }
}

@each $key, $value in borders.$border-styles {
    .#{g.$prefix-cls}-bt-#{$value} {
        border-top-style: $value;
    }
}

@each $key, $value in borders.$border-styles {
    .#{g.$prefix-cls}-br-#{$value} {
        border-right-style: $value;
    }
}

@each $key, $value in borders.$border-styles {
    .#{g.$prefix-cls}-bb-#{$value} {
        border-bottom-style: $value;
    }
}

@each $key, $value in borders.$border-styles {
    .#{g.$prefix-cls}-bl-#{$value} {
        border-left-style: $value;
    }
}

@each $key, $value in borders.$outline {
    .#{g.$prefix-cls}-#{$key} {
        outline: $value;
    }
}

@each $key, $value in borders.$outline-offset {
    .#{g.$prefix-cls}-#{$key} {
        outline-offset: $value;
    }
}


@each $key, $value in borders.$outline-style {
    .#{g.$prefix-cls}-#{$key} {
        outline-style: $value;
    }
}