@use "../config/flex_system" as flex_system;
@use "../config/g_prefix" as g;

@each $key, $value in flex_system.$flex {
    .#{g.$prefix-cls}-#{$key} {
        flex: $value;
    }
}

@each $key, $value in flex_system.$flex-dir {
    .#{g.$prefix-cls}-#{$key} {
        flex-direction: $value;
    }
}

@each $key, $value in flex_system.$flex-wrap {
    .#{g.$prefix-cls}-#{$key} {
        flex-wrap: $value;
    }
}

@each $key, $value in flex_system.$flex-grow {
    .#{g.$prefix-cls}-#{$key} {
        flex-grow: $value;
    }
}

@each $key, $value in flex_system.$flex-shrink {
    .#{g.$prefix-cls}-#{$key} {
        flex-shrink: $value;
    }
}

//-------justify and align, place 

@each $key, $value in flex_system.$justify-content {
    .#{g.$prefix-cls}-#{$key} {
        justify-content: #{$value};
    }
}

@each $key, $value in flex_system.$justify-items {
    .#{g.$prefix-cls}-#{$key} {
        justify-items: #{$value};
    }
}

@each $key, $value in flex_system.$justify-self {
    .#{g.$prefix-cls}-#{$key} {
        justify-self: #{$value};
    }
}

@each $key, $value in flex_system.$align-content {
    .#{g.$prefix-cls}-#{$key} {
        align-content: #{$value};
    }
}

@each $key, $value in flex_system.$align-items {
    .#{g.$prefix-cls}-#{$key} {
        align-items: #{$value};
    }
}

@each $key, $value in flex_system.$align-self {
    .#{g.$prefix-cls}-#{$key} {
        align-self: #{$value};
    }
}

@each $key, $value in flex_system.$place-content {
    .#{g.$prefix-cls}-#{$key} {
        place-content: #{$value};
    }
}

@each $key, $value in flex_system.$place-items {
    .#{g.$prefix-cls}-#{$key} {
        place-items: #{$value};
    }
}

@each $key, $value in flex_system.$place-self {
    .#{g.$prefix-cls}-#{$key} {
        place-self: #{$value};
    }
}