@use "../config/g_prefix" as g;
@use "../config/mask" as mask;

@each $key, $val in mask.$mask-clip {
    .#{g.$prefix-cls}-#{$key} {
        mask-clip: $val;
    }
}

@each $key, $val in mask.$mask-composite {
    .#{g.$prefix-cls}-#{$key} {
        mask-composite: $val;
    }
}

@each $key, $val in mask.$mask-size {
    .#{g.$prefix-cls}-#{$key} {
        mask-size: $val;
    }
}

@each $key, $val in mask.$mask-repeat {
    .#{g.$prefix-cls}-#{$key} {
        mask-repeat: $val;
    }
}

@each $key, $val in mask.$mask-position {
    .#{g.$prefix-cls}-#{$key} {
        mask-position: $val;
    }
}

@each $key, $val in mask.$mask-origin {
    .#{g.$prefix-cls}-#{$key} {
        mask-origin: $val;
    }
}

@each $key, $val in mask.$mask-mode {
    .#{g.$prefix-cls}-#{$key} {
        mask-mode: $val;
    }
}