@use "../config/g_prefix" as g;
@use "../config/typography" as typography;

html body {
    font-size: var(--bnds-g-font-size-14);
}
 

.#{g.$prefix-cls}-mid {
    display: flex;
    justify-content: center;
    align-items: center;
}

.#{g.$prefix-cls}-mid-h {
    display: flex;
    align-items: center;
}

.#{g.$prefix-cls}-mid-v {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.#{g.$prefix-cls}-truncate {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 100%;
}

.#{g.$prefix-cls}-truncate-all {
    max-width: 100%;

    * {
        max-width: 100%;
        white-space: nowrap;
        display: block;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}
  
$thin-scroll-trackbar-radius: var(--bnds-g-border-radius-8);
$thin-scroll-trackbar-bg-hover: var(--bnds-g-color-gray-40);
$thin-scroll-vartical-width: 8px;
$thin-scroll-horizontal-height: 8px;

.thin-scroll::-webkit-scrollbar {
    width: $thin-scroll-vartical-width;
    border-radius: 0;
    height: $thin-scroll-horizontal-height;
}

.thin-scroll::-webkit-scrollbar-thumb {
    background: transparent;
    border-radius: $thin-scroll-trackbar-radius;
}

.thin-scroll::-webkit-scrollbar-track {
    background-color: transparent;
    border-radius: $thin-scroll-trackbar-radius;
}


.thin-scroll:hover::-webkit-scrollbar-thumb {
    background: $thin-scroll-trackbar-bg-hover;
    border-radius: $thin-scroll-trackbar-radius;
}

.#{g.$prefix-cls}-card {
    box-shadow: var(--bnds-g-shadow-5);
    border-radius: var(--bnds-g-border-radius-16);
    background-color: var(--bnds-g-color-white);
    color: var(--bnds-g-color-gray-80);
    display: flex;
    flex-direction: column;

    .#{g.$prefix-cls}-card-heading & {
        font-weight: var(--bnds-g-font-weight-semibold);
        font-size: var(--bnds-g-font-size-20);
    }

    .#{g.$prefix-cls}-card-body & {
        font-size: var(--bnds-g-font-size-14);
        @extend .thin-scroll;
        flex: 1;
        min-height: 1px;
    }

    .#{g.$prefix-cls}-card-footer & {
        font-size: var(--bnds-g-font-size-12);
        color: var(--bnds-g-color-gray-60);
    }
} 