@use "../config/background" as background;
@use "../config/g_prefix" as g;

@each $key, $value in background.$bg-attachment {
    .#{g.$prefix-cls}-#{$key} {
        background-attachment: $value;
    }
}

@each $key, $value in background.$bg-clip {
    .#{g.$prefix-cls}-#{$key} {
        background-clip: $value;
    }
}

@each $key, $value in background.$background-image {
    .#{g.$prefix-cls}-#{$key} {
        background-image: $value;
    }
}

@each $key, $value in background.$background-origin {
    .#{g.$prefix-cls}-#{$key} {
        background-origin: $value;
    }
}

@each $key, $value in background.$background-repeat {
    .#{g.$prefix-cls}-#{$key} {
        background-repeat: $value;
    }
}

@each $key, $value in background.$background-size {
    .#{g.$prefix-cls}-#{$key} {
        background-size: $value;
    }
}

@each $key, $value in background.$background-position {
    .#{g.$prefix-cls}-#{$key} {
        background-position: $value;
    }
}

