/* ----------------------------------
   Overflow Utilities
---------------------------------- */
$overflow: (
    // Shorthand
    o-a: auto,
    o-h: hidden,
    o-c: clip,
    o-v: visible,
    o-s: scroll,

    // Overflow X
    o-xa: auto,
    o-xh: hidden,
    o-xc: clip,
    o-xv: visible,
    o-xs: scroll,

    // Overflow Y
    o-ya: auto,
    o-yh: hidden,
    o-yc: clip,
    o-yv: visible,
    o-ys: scroll,
);
