@use "../config/transform" as transform;
@use "../config/g_prefix" as g;

@each $key, $value in transform.$transform-scale {
    .#{g.$prefix-cls}-#{$key} {
        transform: $value;
    }
}

@each $key, $value in transform.$rotate {
    .#{g.$prefix-cls}-#{$key} {
        transform: $value;
    }
}

@each $key, $value in transform.$trans-origin {
    .#{g.$prefix-cls}-#{$key} {
        transform-origin: $value;
    }
}

@each $key, $value in transform.$trans-style {
    .#{g.$prefix-cls}-#{$key} {
        transform-style: $value;
    }
}

@each $key, $value in transform.$perspective-origin {
    .#{g.$prefix-cls}-#{$key} {
        perspective-origin: $value;
    }
}

@each $key, $value in transform.$perspective {
    .#{g.$prefix-cls}-#{$key} {
        perspective: $value;
    }
}

@each $key, $value in transform.$backface {
    .#{g.$prefix-cls}-#{$key} {
        backface-visibility: $value;
    }
}