@use '../config/spacing' as spacing;
@use '../config/breakpoints' as breakpoints;
@use "../config/g_prefix" as g;

// ----------------------------------------
// 1. Base Utilities (lowest specificity)
// ----------------------------------------
@each $key, $value in spacing.$spacing {
    .#{g.$prefix-cls}-pa-#{$key} {
        padding: var(#{g.$prefix-spacing}-#{$key});
    }

    .#{g.$prefix-cls}-pt-#{$key} {
        padding-top: var(#{g.$prefix-spacing}-#{$key});
    }

    .#{g.$prefix-cls}-pb-#{$key} {
        padding-bottom: var(#{g.$prefix-spacing}-#{$key});
    }

    .#{g.$prefix-cls}-pl-#{$key} {
        padding-left: var(#{g.$prefix-spacing}-#{$key});
    }

    .#{g.$prefix-cls}-pr-#{$key} {
        padding-right: var(#{g.$prefix-spacing}-#{$key});
    }

    .#{g.$prefix-cls}-pr-#{$key} {
        padding-right: var(#{g.$prefix-spacing}-#{$key});
    }

    .#{g.$prefix-cls}-ph-#{$key} {
        padding-right: var(#{g.$prefix-spacing}-#{$key});
        padding-left: var(#{g.$prefix-spacing}-#{$key});
    }

    .#{g.$prefix-cls}-pv-#{$key} {
        padding-top: var(#{g.$prefix-spacing}-#{$key});
        padding-bottom: var(#{g.$prefix-spacing}-#{$key});
    }

}

// ----------------------------------------
// 2. Media Query Utilities (medium specificity)
// ----------------------------------------
@each $bp, $bp-size in breakpoints.$breakpoints {
    @media #{ $bp-size } {
        @each $key, $value in spacing.$spacing {
            .#{$bp}\:#{g.$prefix-cls}-pa-#{$key} {
                padding: var(#{g.$prefix-spacing}-#{$key});
            }

            .#{$bp}\:#{g.$prefix-cls}-pt-#{$key} {
                padding-top: var(#{g.$prefix-spacing}-#{$key});
            }

            .#{$bp}\:#{g.$prefix-cls}-pb-#{$key} {
                padding-bottom: var(#{g.$prefix-spacing}-#{$key});
            }

            .#{$bp}-#{g.$prefix-cls}-pl-#{$key} {
                padding-left: var(#{g.$prefix-spacing}-#{$key});
            }

            .#{$bp}\:#{g.$prefix-cls}-pr-#{$key} {
                padding-right: var(#{g.$prefix-spacing}-#{$key});
            }
        }
    }
}

