@each $key, $value in grid.$gap {
    .#{g.$prefix-cls}-grid-gap-#{$key} {
        gap: ($value); 
    }

    .#{g.$prefix-cls}-grid-col-gap-#{$key} {
        column-gap: ($value);
    }

    .#{g.$prefix-cls}-grid-row-gap-#{$key} {
        row-gap: ($value);
    }
}

@each $key, $value in grid.$grid-rows {
    .#{g.$prefix-cls}-#{$key} {
        grid-template-rows: repeat($value, minmax(0, 1fr));
    }
}

@each $key, $value in grid.$grid-columns {
    .#{g.$prefix-cls}-#{$key} {
        grid-template-columns: repeat($value, minmax(0, 1fr));
    }
}

@each $key, $value in grid.$grid-auto-flow {
    .#{g.$prefix-cls}-#{$key} {
        grid-auto-flow: $value;
    }
}

@each $key, $value in grid.$grid-auto-columns {
    .#{g.$prefix-cls}-#{$key} {
        grid-auto-columns: $value;
    }
}

@each $key, $value in grid.$grid-auto-rows {
    .#{g.$prefix-cls}-#{$key} {
        grid-auto-rows: $value;
    }
}

@each $key, $value in grid.$grid-justify-items {
    .#{g.$prefix-cls}-#{$key} {
        justify-items: $value;
    }
}