$transform-scale: (
    ts-50: scale(0.5),
    ts-100: scale(1),
    ts-120: scale(1.2),
    ts-140: scale(1.4),
    ts-none: none,
);

$rotate: (
    tr-20: rotate(20deg),
    tr-40: rotate(40deg),
    tr-60: rotate(60deg),
    tr-90: rotate(90deg),
    tr-100: rotate(100deg),
    tr-180: rotate(180deg),
    tr-360: rotate(360deg),
);

$backface: (
    bf-h: hidden,
    bf-v: visible
);

$perspective: (
    p-small: 3rem,
    p-mid: 5rem,
    p-lg: 8rem,
    p-xl: 12rem,
    p-none: none,
);

$perspective-origin: (
    po-c: center,
    po-t: top,
    po-tr : top right,
    po-ri : right,
    po-br : bottom right,
    po-b: bottom,
    po-bl : bottom left,
    po-l: left,
    po-tl: top left
);

$trans-origin: (
    to-c: center,
    to-t: top,
    to-tr: top right,
    to-r: right,
    to-br: bottom right,
    to-b: bottom,
    to-bl: bottom left,
    to-l: left,
    to-tl: top left,
);

$trans-style: (
    tr-sty-3d: preserve-3d,
    tr-sty-flat: flat,
);

