$table-layout: (
    tl-a: auto,
    tl-f: fixed,
);

$caption-side: (
    cs-t: top,
    cs-b: bottom,
);

$border-spacing: (
    bs-0: 0,
    bs-1: .0625rem,
    bs-2: .125rem,
    bs-3: .1875rem,
    bs-4: .25rem,
    bs-8: .5rem,
    bs-12: .75rem,

);
$border-collapse: (
    bc-c: collapse,
    bc-s: separate,
);
$empty-cells: (
    ec-s: show,
    ec-h: hide,
);
 