 @use 'sass:map';
 @use 'sass:math';
 @use'../config/breakpoints' as breakpoints;
 @use'../config/g_prefix' as g;




 @function color-yiq($color) {
   $r: red($color);
   $g: green($color);
   $b: blue($color);

   $yiq: (
     ($r * 299) + ($g * 587) + ($b * 114)) / 1000;

   @if ($yiq >=$yiq-contrasted-threshold) {
     @return $yiq-text-dark;
   }

   @else {
     @return $yiq-text-light;
   }
 }


 //return values
 @function get-value($name, $breakpoints: $grid-breakpoints) {
   $min: map.get($breakpoints, $name
   );
 @return if($min !=0, $min, null);
 }

 //return keys
 @function get-key($name, $breakpoints: $grid-breakpoints) {
   @return if(get-value($name, $breakpoints)==null, "", "-#{$name}");
 }

 //set media query
 @mixin set-media-width($name, $breakpoints: $grid-breakpoints) {
   $min: get-value($name, $breakpoints);

   @if $min {
     @media (min-width: $min) {
       @content;
     }
   }

   @else {
     @content;
   }
 }

 /*
Conver Pixel to rem
*/

 @function pxToRem($value) {
   @return math.div($value, 16) * 1rem;
 }


 @mixin set-media($first-cls-name, $last-cls-name, $collection-name, $property, $breakpoints) {
   ///ex: @include function.set-media(first-cls-name, -last-cls-name, display.$display, display, bp.$container-breakpoints);
   //****for no class-name then pass null value****//
   ///ex: @include function.set-media(null, -last-cls-name, display.$display, display, bp.$container-breakpoints);

   $cls1: null;

   @if $first-cls-name {
     $cls1: #{-$first-cls-name};
   }

   @each $bp, $bp-val in $breakpoints {
     @media (min-width: #{$bp-val}) {
       @each $key, $val in $collection-name {
         .#{$bp}\:#{g.$prefix-cls}#{$cls1}-#{$key}#{$last-cls-name} {
           #{$property}: $val;
         }
       }
     }
   }
 }

 @mixin set-responsive(
   $first-cls-name,
   $last-cls-name,
   $collection-name,
   $property,
   $value,
   $isVarValue,
   $breakpoints ) {
   ///ex: @include function.set-media(first-cls-name, -last-cls-name, display.$display, display, bp.$container-breakpoints);
   //****for no class-name then pass null value****//
   ///ex: @include function.set-media(null, -last-cls-name, display.$display, display, bp.$container-breakpoints);

   $cls1: null;

   @if $first-cls-name {
     $cls1: #{-$first-cls-name};
   }

   @each $bp, $bp-val in $breakpoints {
     @media #{$bp-val} {
       @each $key, $val in $collection-name {

         $final-value: $val;

         @if $value !=null {
           $final-value: $value;
         }

         @if $isVarValue and $value ==null {
           $final-value: var(#{g.$prefix-var}-$key);
         }

         .#{$bp}\:#{g.$prefix-cls}#{$cls1}-#{$key}#{$last-cls-name} {
           #{$property}: $final-value;
         }
       }
     }
   }
 }
 