/* ----------------------------------
   Display Utilities
---------------------------------- */
$display: (
    //--- Common layout
    d-block: block,
    d-inline-block: inline-block,
    d-inline: inline,
    d-list-item: list-item,
    d-none: none,
    d-flow-root: flow-root,
    d-contents: contents,


    //--- Flexbox
    d-flex: flex,
    d-inline-flex: inline-flex,

    // Grid
    d-grid: grid,
    d-inline-grid: inline-grid,

    //--- Table Display 

    d-table : table,
    d-inline-table : inline-table,
    d-table-caption : table-caption,
    d-table-cell : table-cell,
    d-table-column : table-column,
    d-table-column-group : table-column-group,
    d-table-header-group : table-header-group,
    d-table-footer-group : table-footer-group,
    d-table-row-group : table-row-group,
    d-table-row : table-row
);