@use "../config/g_prefix" as g;
@use "../config/sizing" as sizing; 
@use "../mixins/xcalc" as xcalc;

@each $key, $value in sizing.$sizing {
    .#{g.$prefix-cls}-ht-#{$key} { height: var(#{g.$prefix-sizing}-#{$key}); }
    .#{g.$prefix-cls}-min-ht-#{$key} { min-height: var(#{g.$prefix-sizing}-#{$key}); }
    .#{g.$prefix-cls}-max-ht-#{$key} { max-height: var(#{g.$prefix-sizing}-#{$key}); }
}

@each $key, $value in sizing.$sizing-in-per {

    .#{g.$prefix-cls}-h-#{$key} { height: #{$value} }
    .#{g.$prefix-cls}-min-h-#{$key} { min-height: #{$value}; }
    .#{g.$prefix-cls}-max-h-#{$key} { max-height: #{$value}; }

    //----VH
    .#{g.$prefix-cls}-vh-#{$key} { height: #{$key}vh }
    .#{g.$prefix-cls}-min-vh-#{$key} { min-height: #{$key}vh; }
    .#{g.$prefix-cls}-max-vh-#{$key} { max-height: #{$key}vh; }

    .#{g.$prefix-cls}-h-svh-#{$key} { height: #{$key}svh }
    .#{g.$prefix-cls}-h-lvh-#{$key} { min-height: #{$key}lvh; }
    .#{g.$prefix-cls}-h-dvh-#{$key} { max-height: #{$key}dvh; }

}

@for $i from 1 through 10 {
    //  $val-per : $i * 10%;
     $name: $i * 10; 
    .#{g.$prefix-cls}-h-100-#{$i}{ @include xcalc.x-calc(height, 100% - #{$i}rem)}
    .#{g.$prefix-cls}-vh-100-#{$i}{@include xcalc.x-calc(height, 100% - #{$i}rem);}
}
 



