@use "../config/g_prefix" as g;
@use "../config/typography" as typography;

@each $key, $value in typography.$font-sizes {
    .#{g.$prefix-cls}-font-#{$key} {
        font-size: var(#{g.$prefix-font}-#{$key});
    }
}

@each $key, $value in typography.$font-weights {
    .#{g.$prefix-cls}-fw-#{$key} {
        font-weight: var(#{g.$prefix-font-weight}-#{$key});
    }

}

@each $key, $value in typography.$line-heights {
    .#{g.$prefix-cls}-lh-#{$key} {
        line-height: var(#{g.$prefix-line-height}-#{$key});
    }
}

@each $key, $value in typography.$font-variant {
    .#{g.$prefix-cls}-#{$key} {
        font-variant: $value;
    }
}

@each $key, $value in typography.$font-style {
    .#{g.$prefix-cls}-#{$key} {
        font-style: $value;
    }
}

@each $key, $value in typography.$letter-spacing {
    .#{g.$prefix-cls}-#{$key} {
        letter-spacing: $value;
    }
}

@each $key, $value in typography.$line-clamp {
    .#{g.$prefix-cls}-#{$key} {
        display: -webkit-box;
        box-orient: vertical;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: $value;
        line-clamp: $value;
        overflow: hidden;

    }
}

@each $key, $value in typography.$list-style-type {
    .#{g.$prefix-cls}-#{$key} {
        list-style-type: $value;
    }

}

@each $key, $value in typography.$list-position {
    .#{g.$prefix-cls}-#{$key} {
        list-style-position: $value;
    }

}

@each $key, $value in typography.$text-align {
    .#{g.$prefix-cls}-#{$key} {
        text-align: $value;
    }

}

@each $key, $value in typography.$text-transform {
    .#{g.$prefix-cls}-#{$key} {
        text-transform: $value;
    }
}

@each $key, $value in typography.$text-decoration {
    .#{g.$prefix-cls}-#{$key} {
        text-decoration: $value;
    }
}

@each $key, $value in typography.$text-decoration-thickness {
    .#{g.$prefix-cls}-#{$key} {
        text-decoration-thickness: $value;
    }
}

@each $key, $value in typography.$text-decoration-style {
    .#{g.$prefix-cls}-#{$key} {
        text-decoration-style: $value;
    }
}

@each $key, $value in typography.$text-underline-offset {
    .#{g.$prefix-cls}-txt-u-offset-#{$key} {
        text-underline-offset: $value;
    }
}

@each $key, $value in typography.$text-wrap {
    .#{g.$prefix-cls}-#{$key} {
        text-wrap: $value;
    }
}

@each $key, $value in typography.$white-space {
    .#{g.$prefix-cls}-#{$key} {
        white-space: $value;
    }
}

@each $key, $value in typography.$word-break {
    .#{g.$prefix-cls}-#{$key} {
        word-break: $value;
    }
}

@each $key, $value in typography.$writing-mode {
    .#{g.$prefix-cls}-wm-#{$key} {
        writing-mode: $value;
    }
}

@each $key, $value in typography.$direction {
    .#{g.$prefix-cls}-dir-#{$key} {
        direction: $value;
        unicode-bidi: bidi-override;
    }
}