@use "../config/scroll" as scroll;
@use "../config/g_prefix" as g;
@use "../mixins/scroll" as slim-scroll;

@each $key, $val in scroll.$s-size {

    //---margin---
    .#{g.$prefix-cls}-scroll-m-#{$key} {
        scroll-margin: $val;
    }

    .#{g.$prefix-cls}-scroll-mt-#{$key} {
        scroll-margin-top: $val;
    }

    .#{g.$prefix-cls}-scroll-mr-#{$key} {
        scroll-margin-right: $val;
    }

    .#{g.$prefix-cls}-scroll-mb-#{$key} {
        scroll-margin-bottom: $val;
    }

    .#{g.$prefix-cls}-scroll-ml-#{$key} {
        scroll-margin-left: $val;
    }

    //---margin-block---

    .#{g.$prefix-cls}-scroll-mb-#{$key} {
        scroll-margin-block: $val;
    }

    .#{g.$prefix-cls}-scroll-mbe-#{$key} {
        scroll-margin-block-end: $val;
    }

    .#{g.$prefix-cls}-scroll-mbs-#{$key} {
        scroll-margin-block-start: $val;
    }

    //---margin-block---
    .#{g.$prefix-cls}-scroll-mi-#{$key} {
        scroll-margin-inline: $val;
    }

    .#{g.$prefix-cls}-scroll-mie-#{$key} {
        scroll-margin-inline-end: $val;
    }

    .#{g.$prefix-cls}-scroll-mis-#{$key} {
        scroll-margin-inline-start: $val;
    }

    //---padding---
    .#{g.$prefix-cls}-scroll-p-#{$key} {
        scroll-padding: $val;
    }

    .#{g.$prefix-cls}-scroll-pt-#{$key} {
        scroll-padding-top: $val;
    }

    .#{g.$prefix-cls}-scroll-pr-#{$key} {
        scroll-padding-right: $val;
    }

    .#{g.$prefix-cls}-scroll-pb-#{$key} {
        scroll-padding-bottom: $val;
    }

    .#{g.$prefix-cls}-scroll-pl-#{$key} {
        scroll-padding-left: $val;
    }

    //---padding-block---

    .#{g.$prefix-cls}-scroll-p-block-#{$key} {
        scroll-padding-block: $val;
    }

    .#{g.$prefix-cls}-scroll-p-block-end-#{$key} {
        scroll-padding-block-end: $val;
    }

    .#{g.$prefix-cls}-scroll-p-block-start-#{$key} {
        scroll-padding-block-start: $val;
    }

    //---padding-block---
    .#{g.$prefix-cls}-scroll-p-inline-#{$key} {
        scroll-padding-inline: $val;
    }

    .#{g.$prefix-cls}-scroll-p-inline-end-#{$key} {
        scroll-padding-inline-end: $val;
    }

    .#{g.$prefix-cls}-scroll-p-inline-start-#{$key} {
        scroll-padding-inline-start: $val;
    }
}

@each $key, $val in scroll.$snap-align {
    .#{g.$prefix-cls}-scroll-sa-#{$key} {
        scroll-snap-align: $val;
    }
}

@each $key, $val in scroll.$snap-behavior {
    .#{g.$prefix-cls}-scroll-sb-#{$key} {
        scroll-behavior: $val;
    }
}

@each $key, $val in scroll.$snap-type {
    .#{g.$prefix-cls}-scroll-#{$key} {
        scroll-snap-type: $val;
    }
}

@each $key, $val in scroll.$snap-stop {
    .#{g.$prefix-cls}-scroll-#{$key} {
        scroll-snap-stop: $val;
    }
} 

@include slim-scroll.slim-scroll('.slim-scroll', var(--neutral-80));