@use "../config/interactivity" as interactivity;
@use "../config/g_prefix" as g;
@use "../mixins/appearance" as appearance;

@each $key, $value in interactivity.$accent-color {
    .#{g.$prefix-cls}-#{$key} {
        accent-color: $value;
    }
}

@each $key, $value in interactivity.$appearance {
    .#{g.$prefix-cls}-#{$key} {
        @include appearance.x-appearance($value);
    }
}

@each $key, $value in interactivity.$color-scheme {
    .#{g.$prefix-cls}-#{$key} {
        color-scheme: #{$value};
    }
}

@each $key, $value in interactivity.$user-select {
    .#{g.$prefix-cls}-#{$key} {
        user-select: $value;
    }
}

@each $key, $value in interactivity.$resize {
    .#{g.$prefix-cls}-#{$key} {
        resize: $value;
    }
}