@use "../config/shadows" as shadows;
@use "../config/g_prefix" as g;

@each $key, $value in shadows.$shadows {
   .#{g.$prefix-cls}-bx-sh-#{$key} {
        box-shadow: #{$value};
    }
}

@each $key, $value in shadows.$t-shadows {
  .#{g.$prefix-cls}-txt-sh-#{$key} {
        text-shadow: $value;
    }
}