/* ----------------------------------
   Mix Blend Modes
---------------------------------- */
$mix-blend: (
    mxb-n: 'normal',
    mxb-m: 'multiply',
    mxb-scr: 'screen',
    mxb-o: 'overlay',
    mxb-dar: 'darken',
    mxb-l: 'lighten',
    mxb-cd: 'color-dodge',
    mxb-cb: 'color-burn',
    mxb-hl: 'hard-light',
    mxb-sl: 'soft-light',
    mxb-dif: 'difference',
    mxb-e: 'exclusion',
    mxb-h: 'hue',
    mxb-sat: 'saturation',
    mxb-c: 'color',
    mxb-lum: 'luminosity'
);

