/* ----------------------------------
   1. Background Image
---------------------------------- */
$background-image: (
    bg-img-n: none,
);

/* ----------------------------------
   2. Background Position
---------------------------------- */
$background-position: (
    bgp-tl: top left,
    bgp-t: top,
    bgp-tr: top right,
    bgp-l: left,
    bgp-c: center,
    bgp-r: right,
    bgp-bl: bottom left,
    bgp-b: bottom,
    bgp-br: bottom right,
);

/* ----------------------------------
   3. Background Size
---------------------------------- */
$background-size: (
    bgs-a: auto,
    bgs-cov: cover,
    bgs-con: contain
);

/* ----------------------------------
   4. Background Repeat
---------------------------------- */
$background-repeat: (
    bgr-rep: repeat,
    bgr-nr: no-repeat,
    bgr-x: repeat-x,
    bgr-y: repeat-y,
    bgr-s: space,
    bgr-rou: round,
);

/* ----------------------------------
   5. Background Origin
---------------------------------- */
$background-origin: (
    bgo-bb : 'border-box',
    bgo-pb : 'padding-box',
    bgo-cb : 'content-box',
);

/* ----------------------------------
   6. Background Clip
---------------------------------- */
$bg-clip: (
    bgc-bb: 'border-box',
    bgc-pb: 'padding-box',
    bgc-cb: 'content-box',
    bgc-t: 'text'
);

/* ----------------------------------
   7. Background Attachment
---------------------------------- */
$bg-attachment: (
    bga-f: 'fixed',
    bga-l: 'local',
    bga-s: 'scroll',
);
