@use '../config/g_prefix' as g;
@use '../config/spacing' as spacing;
@use '../config/breakpoints' as breakpoints;
@use '../mixins/function' as f;

// ----------------------------------------
// 1. Base Utilities (lowest specificity)
// ----------------------------------------
@each $key, $value in spacing.$spacing {
    .#{g.$prefix-cls}-ma-#{$key} {
        margin: var(#{g.$prefix-spacing}-#{$key});
    }

    .#{g.$prefix-cls}-mh-#{$key} {
        margin-left: var(#{g.$prefix-spacing}-#{$key});
        margin-right: var(#{g.$prefix-spacing}-#{$key});
    }

    .#{g.$prefix-cls}-mv-#{$key} {
        margin-top: var(#{g.$prefix-spacing}-#{$key});
        margin-bottom: var(#{g.$prefix-spacing}-#{$key});
    }

    .#{g.$prefix-cls}-mt-#{$key} {
        margin-top: var(#{g.$prefix-spacing}-#{$key});
    }

    .#{g.$prefix-cls}-mb-#{$key} {
        margin-bottom: var(#{g.$prefix-spacing}-#{$key});
    }

    .#{g.$prefix-cls}-ml-#{$key} {
        margin-left: var(#{g.$prefix-spacing}-#{$key});
    }

    .#{g.$prefix-cls}-mr-#{$key} {
        margin-right: var(#{g.$prefix-spacing}-#{$key});
    }


}

// ----------------------------------------
// 2. Media Query Utilities (medium specificity)
// ----------------------------------------

@each $bp, $bp-size in breakpoints.$breakpoints {
    @media #{ $bp-size } {
        @each $key, $value in spacing.$spacing {
            .#{$bp}\:#{g.$prefix-cls}-ma-#{$key} {
                margin: var(#{g.$prefix-spacing}-#{$key});
            }

            .#{$bp}\:#{g.$prefix-cls}-mv-#{$key} {
                margin-top: var(#{g.$prefix-spacing}-#{$key});
                margin-bottom: var(#{g.$prefix-spacing}-#{$key});
            }

            .#{$bp}\:#{g.$prefix-cls}-mh-#{$key} {
                margin-left: var(#{g.$prefix-spacing}-#{$key});
                margin-right: var(#{g.$prefix-spacing}-#{$key});
            }

            .#{$bp}\:#{g.$prefix-cls}-mt-#{$key} {
                margin-top: var(#{g.$prefix-spacing}-#{$key});
            }

            .#{$bp}\:#{g.$prefix-cls}-mb-#{$key} {
                margin-bottom: var(#{g.$prefix-spacing}-#{$key});
            }

            .#{$bp}\:#{g.$prefix-cls}-ml-#{$key} {
                margin-left: var(#{g.$prefix-spacing}-#{$key});
            }

            .#{$bp}\:#{g.$prefix-cls}-mr-#{$key} {
                margin-right: var(#{g.$prefix-spacing}-#{$key});
            }


        }
    }
}