/* ----------------------------------
Cursor Utilities
---------------------------------- */
$cursor: (
    // General
    cur-a: 'auto',
    cur-d: 'default',
    cur-n: 'none',
    cur-h: 'help',
    cur-cm: 'context-menu',

    // Click / Action
    cur-p: 'pointer',
    cur-na: 'not-allowed',
    cur-nd: 'no-drop',
    cur-c: 'copy',
    cur-ali: 'alias',

    // Loading / Waiting
    cur-w: 'wait',
    cur-pro: 'progress',

    // Text / Selection
    cur-t: 'text',
    cur-cro: 'crosshair',
    cur-ce: 'cell',

    // Movement / Dragging
    cur-m: 'move',
    cur-grb: 'grab',
    cur-gbin: 'grabbing',
    cur-as: 'all-scroll',

    // Resize - single direction
    cur-nr: 'n-resize',
    cur-sr: 's-resize',
    cur-er: 'e-resize',
    cur-wr: 'w-resize',

    // Resize - two-direction (linear)
    cur-nsr: 'ns-resize',
    cur-ewr: 'ew-resize',
    cur-rr: 'row-resize',
    cur-cr: 'col-resize',

    // Resize - diagonal
    cur-ner: 'ne-resize',
    cur-nwr: 'nw-resize',
    cur-ser: 'se-resize',
    cur-swr: 'sw-resize',

    // Resize - opposite diagonals
    cur-neswr: 'nesw-resize',
    cur-nwser: 'nwse-resize',

    // Zoom
    cur-z-i: 'zoom-in',
    cur-z-o: 'zoom-out'
);


/* ----------------------------------
Pointer Events
---------------------------------- */
$pointer: (
    p-auto: auto,
    p-none: none
);