@use "../config/animation" as animation;
@use "../config/g_prefix" as g;
@use "../mixins/transition" as trans;
@use "../mixins/animation" as anim;


// spin 1s linear infinite  

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

// ping 1s cubic-bezier(0, 0, 0.2, 1) infinite  

@keyframes ping {

    75%,
    100% {
        transform: scale(2);
        opacity: 0;
    }
}

// pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite  

@keyframes pulse {
    50% {
        opacity: 0.5;
    }
}

// bounce 1s infinite  

@keyframes bounce {

    0%,
    100% {
        transform: translateY(-25%);
        animation-timing-function: cubic-bezier(0.8, 0, 1, 1);
    }

    50% {
        transform: none;
        animation-timing-function: cubic-bezier(0, 0, 0.2, 1);
    }
}

@keyframes topLeft {
    0% {
        margin-top: -6.25rem;
        opacity: 0;
    }

    100% {
        margin-top: .625rem;
        opacity: 1;
    }
}

@keyframes topRight {
    0% {
        margin-right: -6.25rem;
        opacity: 0;
    }

    100% {
        margin-right: .625rem;
        opacity: 1;
    }
}

@keyframes bottomLeft {
    0% {
        margin-left: -6.25rem;
        opacity: 0;
    }

    100% {
        margin-left: .625rem;
        opacity: 1;
    }
}

@keyframes bottomRight {
    0% {
        margin-right: -6.25rem;
        opacity: 0;
    }

    100% {
        margin-right: .625rem;
        opacity: 1;
    }
}

@keyframes expandVertical {
    0% {
        height: 0;  
        overflow: hidden;
    }

    100% {
        height: auto;  
        overflow: initial
    }
}
@keyframes expandHorizontal {
    0% {
        width: 0;
        opacity: 0;
    }

    100% {
        width: auto;
        opacity: 1;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadeOut {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}


@each $key, $value in animation.$animation {
    .#{g.$prefix-cls}-#{$key} {
        @include anim.animation($value);
    }
}

@each $key, $value in animation.$transition {
    .#{g.$prefix-cls}-#{$key} {
        @include trans.transition($value);
    }
}