$font-sizes: (
    //8: 0.5rem,
    // 8px
    10: 0.625rem,
    // 10px
    12: 0.75rem,
    // 12px
   // 13: 0.8125rem,
    // 13px
    14: 0.875rem,
    // 14px
    16: 1rem,
    // 16px
    20: 1.25rem,
    // 20px
    24: 1.5rem,
    // 24px
    28: 1.75rem,
    // 28px
    32: 2rem,
    // 32px
    36: 2.25rem,
    // 36px
    40: 2.5rem,
    // 40px
    48: 3rem,
    // 48px
);

$headings: (
    h1:36,
    h2:28,
    h3:24,
    h4:20,
    h5:16,
    h6:14
);

$font-weights: (
    
    light: 300, // light
    regular: 400, // regular
    medium: 500, // medium
    semibold: 600, // semibold
    bold: 700, // bold
    bolder: 800, // bolder
);

$line-heights: (
    1: 1,
    2: 1.25,
    3: 1.375,
    4: 1.5,
    5: 1.75,
    6: 2,
    7: 2.25,
    8: 2.5
);
$font-variant: (
    fv-normal: normal,
    fv-ordinal: ordinal,
    fv-slashed-zero: slashed-zero,
    fv-lining-nums: lining-nums,
    fv-oldstyle-nums: oldstyle-nums,
    fv-proportional-nums: proportional-nums,
    fv-tabular-nums: tabular-nums,
    fv-diagonal-fractions: diagonal-fractions,
    fv-stacked-fractions: stacked-fractions,
);
$font-style: (
    fs-normal : normal,
    fs-italic: italic,
    fs-oblique: oblique,
);

$letter-spacing: (
    ls-tighter : -0.05em,
    ls-tight : -0.025,
    ls-normal : 0rem,
    ls-wide : 0.025re,
    ls-wider: 0.05rem,
    ls-widest: 0.1rem
);

$line-clamp: (
    ln-clamp-1: 1,
    ln-clamp-2: 2,
    ln-clamp-3: 3,
    ln-clamp-4: 4,
    ln-clamp-5: 5,
);

$list-style-type: (
    li-disc: disc,
    li-circle: circle,
    li-square: square,
    li-decimal: decimal,
    li-lower-alpha: lower-alpha,
    li-upper-alpha: upper-alpha,
    li-decimal-leading-zero: decimal-leading-zero
);
$list-position: (
    li-i: inside,
    li-o: outside
);

$text-align: (
    txt-l: left,
    txt-c: center,
    txt-r: right
);
$text-transform: (
    txt-t-u: uppercase,
    txt-t-l: lowercase,
    txt-t-c: capitalize
);
$text-decoration: (
    txt-d-u: underline,
    txt-d-lt: line-through,
    txt-d-o: overline,
    txt-d-uo: underline overline,
    txt-d-n: none
);
$text-decoration-thickness: (
    txt-dt-thin: thin,
    txt-dt-medium: medium,
    txt-dt-thick: thick
);

$text-decoration-style: (
    txt-ds-solid: solid,
    txt-ds-dashed: dashed,
    txt-ds-dotted: dotted,
    txt-ds-double: double,
    txt-ds-wavy: wavy
);

$text-underline-offset: (
    auto: auto,
    0: 0,
    1: .063rem,
    2: .125rem,
    3: .188,
    4: .25rem,
    8: .5rem,

);

$text-wrap: (
    txt-w-nowrap: nowrap,
    txt-w-wrap: wrap,
    txt-w-balanced : balanced,
    txt-w-pretty : pretty,
);

$white-space: (
    ws-normal: normal,
    ws-nowrap: nowrap,
    ws-pre: pre,
    ws-pre-line: pre-line,
    ws-pre-wrap: pre-wrap,
    ws-break-space: break-spaces,
);

$word-break: (
    wb-normal: normal,
    wb-break-all: break-all,
    wb-keep-all: keep-all,
);

$writing-mode: (
    wm-h: horizontal-tb,
    wm-v: vertical-rl,
);

$direction: (
    dir-ltr: ltr,
    dir-rtl: rtl,
    dir-ini: initial,
);