$s-size: (
    0:0,
    8:0.5rem,
    16:1rem,
    20:1.5rem,
    32:2rem,
    48:3rem
);

$snap-behavior: (
    sb-a:auto,
    sb-s:smooth
);

$snap-align: (
    sa-s: start,
    sa-e: end,
    sa-c: center,
    sa-n: none,
);
 
$snap-type: (
    st-n: none,
    st-xm: x mandatory,
    st-ym: y mandatory,
    st-bom: both mandatory,
    st-blm: block mandatory,
    st-im: inline mandatory,

);

$snap-stop: (
    ss-n: normal,
    ss-a: always,
);
 