@use "../config/g_prefix" as g;
@use "../config/sizing" as sizing; 
@use "../config/width" as width; 
@use "../mixins/xcalc" as xcalc;

@each $key, $value in sizing.$sizing {
    .#{g.$prefix-cls}-wt-#{$key} { width: var(#{g.$prefix-sizing}-#{$key}); }
    .#{g.$prefix-cls}-min-wt-#{$key} { min-width: var(#{g.$prefix-sizing}-#{$key}); }
    .#{g.$prefix-cls}-max-wt-#{$key} { max-width: var(#{g.$prefix-sizing}-#{$key}); }
}

@each $key, $value in sizing.$sizing-in-per {

    .#{g.$prefix-cls}-w-#{$key} { width: #{$value} }
    .#{g.$prefix-cls}-min-w-#{$key} { min-width: #{$value}; }
    .#{g.$prefix-cls}-max-w-#{$key} { max-width: #{$value}; }

    //----VH
    .#{g.$prefix-cls}-vw-#{$key} { width: #{$key}vh }
    .#{g.$prefix-cls}-min-vw-#{$key} { min-width: #{$key}vh; }
    .#{g.$prefix-cls}-max-vw-#{$key} { max-width: #{$key}vh; }

    .#{g.$prefix-cls}-w-hsv-#{$key} { width: #{$key}svh }
    .#{g.$prefix-cls}-w-hlv-#{$key} { min-width: #{$key}lvh; }
    .#{g.$prefix-cls}-w-hdv-#{$key} { max-width: #{$key}dvh; }

}

/* -------------------------------------------------
   Calculated width utilities (100% - n rem)
-------------------------------------------------- */

@for $i from 1 through 10 {
    //  $val-per : $i * 10%;
     $name: $i * 10; 
    .#{g.$prefix-cls}-w-100-#{$i}{@include xcalc.x-calc(width, 100% - #{$i}rem);}
    .#{g.$prefix-cls}-vw-100-#{$i}{@include xcalc.x-calc(width, 100% - #{$i}rem);}
} 



/* -------------------------------------------------
   Keyword-based width utilities
-------------------------------------------------- */
@each $name, $value in width.$width {
    .#{g.$prefix-cls}-w-#{$name} { width: #{$value} } 
    .#{g.$prefix-cls}-min-w-#{$name} { width: min-#{$value} } 
    .#{g.$prefix-cls}-max-w-#{$name} { width: max-#{$value} }
}
 
