@use "../config/rows_columns" as grid;
@use "../config/g_prefix" as g;
@use "../config/breakpoints" as breakpoints;

.#{g.$prefix-cls}-row {
    display: flex;
}

.#{g.$prefix-cls}-col {
    flex: 1;
}

@each $name, $value in grid.$grid-box {

    .#{g.$prefix-cls}-col-size-#{$name} {
        width: #{$value};
        flex: none;
    }

    // .#{g.$prefix-cls}-col-size-#{$name}-of-12 {
    //     width: #{$value};
    //     flex: none;
    // }

}


@each $bp, $bp-size in breakpoints.$breakpoints {
    @media #{ $bp-size } {
        @each $name, $value in grid.$grid-box {

            .#{$bp}\:#{g.$prefix-cls}-col-size-#{$name} {
                width: #{$value};
                flex: none;
            }

            // .#{$bp}\:#{g.$prefix-cls}-col-size-#{$name}-of-12 {
            //     width: #{$value};
            //     flex: none;
            // }

        }
    }
}