$borders-width: (
    0: 0,
    1: .063rem,
    2: .125rem,
    3: .188rem,
    4: .25rem,
);

$borders-radius: (
    4: .25rem,
    // 4px
    8: .5rem,
    // 8px
    12: .75rem,
    // 12px
    16: 1rem,
    // 16px
    20: 1.25rem,
    // 20px
    "circle": 100%,
    "pill": 15rem // Circle border radius
);

$border-styles: (
    bs-solid: solid,
    bs-dashed: dashed,
    bs-dotted: dotted,
    bs-double: double,
    bs-none: none,
    bs-hidden: hidden,
);

///
////---outlines---
///  

$outline: (
    ol-1: .0625rem,
    ol-2: .125rem,
    ol-3: .1875rem,
    ol-4:.25rem,
    ol-0:0,
);

$outline-offset: (
    of-1: .0625rem,
    of-2: .125rem,
    of-3: .1875rem,
    of-4:.25rem,
    of-0:0,
);

$outline-style: (
    os-solid: solid,
    os-dashed: dashed,
    os-dotted: dotted,
    os-double: double,
    os-none: none,  
    os-transparent: transparent
);