$mask-clip: (
    mc-bb: border-box,
    mc-pb: padding-box,
    mc-cb: content-box,
    mc-fb: fill-box,
    mc-sb: stroke-box,
    mc-vb: view-box,
    mc-nc: no-clip,
);

$mask-composite: (
    mc-a: add,
    mc-s: subtract,
    mc-i: intersect,
    mc-e: exclude,
);

$mask-size: (
    ms-a: auto,
    ms-cov: cover,
    ms-con: contain
);

$mask-repeat: (
    ms-rep : repeat,
    ms-nr : no-repeat,
    ms-e-x : repeat-x,
    ms-r-y : repeat-y,
    ms-s : space,
    ms-rou : round,
);

$mask-position: (
    ms-tl: top left,
    ms-t: top,
    ms-tr: top right,
    ms-l: left,
    ms-c: center,
    ms-r: right,
    ms-bl: bottom left,
    ms-b: bottom,
    ms-br: bottom right,
);

$mask-origin: (
    mo-bb: border-box,
    mo-pb: padding-box,
    mo-cb: content-box,
    mo-fb: fill-box,
    mo-sb: stroke-box,
    mo-vb: view-box,
);

$mask-mode: (
    mm-alp: alpha,
    mm-lum: luminance,
    mm-sou: match-source,
);