/* ================= FLEX ================= */

/* Flex Direction */
$flex-dir: (
    fd-row: row,
    fd-row-reverse: row-reverse,
    fd-column: column,
    fd-column-reverse: column-reverse,
);

/* Flex Wrap */
$flex-wrap: (
    fw-nowrap: nowrap,
    fw-wrap: wrap,
    fw-wrap-reverse: wrap-reverse,
);

/* Flex */
$flex: (
    flex-0: 0,
    flex-1: 1,
    flex-2: 2,
    flex-3: 3,
    flex-4: 4,
    flex-5: 5,
    flex-6: 6,
    flex-7: 7,
    flex-8: 8,
    flex-9: 9,
    flex-10: 10,
    flex-11: 11,
    flex-12: 12,
    flex-n: none,
);

/* Flex Grow */
$flex-grow: (
    fg-0: 0,
    fg-1: 1,
    fg-2: 2,
    fg-3: 3,
    fg-4: 4,
    fg-5: 5,
    fg-6: 6,
    fg-7: 7,
    fg-8: 8,
    fg-9: 9,
    fg-10: 10,
    fg-11: 11,
    fg-12: 12,
);

/* Flex Shrink */
$flex-shrink: (
    fs-0: 0,
    fs-1: 1,
    fs-2: 2,
    fs-3: 3,
    fs-4: 4,
    fs-5: 5,
);

/* ================= JUSTIFY ================= */

/* Justify Content */
$justify-content: (
    jc-start: flex-start,
    jc-end: flex-end,
    jc-center: center,
    jc-between: space-between,
    jc-around: space-around,
    jc-evenly: space-evenly,
    jc-stretch: stretch,
    jc-baseline: baseline,
    jc-normal: normal,
);

/* Justify Items */
$justify-items: (
    ji-flex-start: flex-start,
    ji-flex-end: flex-end,
    ji-start: start,
    ji-end: end,
    ji-center: center,
    ji-stretch: stretch,
    ji-normal: normal,
);

/* Justify Self */
$justify-self: (
    js-auto: auto,
    js-start: start,
    js-end: end,
    js-center: center,
    js-stretch: stretch,
);

/* ================= ALIGN ================= */

/* Align Content */
$align-content: (
    ac-normal: normal,
    ac-center: center,
    ac-flex-start: flex-start,
    ac-flex-end: flex-end,
    ac-between: space-between,
    ac-around: space-around,
    ac-evenly: space-evenly,
    ac-baseline: baseline,
    ac-stretch: stretch,
);

/* Align Items */
$align-items: (
    ai-flex-start : flex-start,
    ai-flex-end : flex-end,
    ai-center : center,
    ai-baseline : baseline,
    ai-last-baseline : last-baseline,
    ai-stretch : stretch,
);

/* Align Self */
$align-self: (
    as-auto: auto,
    as-flex-start: flex-start,
    as-flex-end: flex-end,
    as-center:center,
    as-baseline: baseline,
    as-last-baseline: last-baseline,
    as-stretch: stretch,
);

/* ================= PLACE ================= */

/* Place Content */
$place-content: (
    pc-start: start,
    pc-end: end,
    pc-center: center,
    pc-space-between: space-between,
    pc-space-around: space-around,
    pc-space-evenly: space-evenly,
    pc-baseline: baseline,
    pc-stretch: stretch,
);

/* Place Items */
$place-items: (
    pi-start : start,
    pi-end : end,
    pi-center : center,
    pi-baseline : baseline,
    pi-stretch : stretch,
);

/* Place Self */
$place-self: (
    ps-start: start,
    ps-end: end,
    ps-center: center,
    ps-baseline: baseline,
    ps-stretch: stretch,
);