$grid: (
    col-1: calc(100% / 12),
    col-2: calc(100% / 12 * 2),
    col-3: calc(100% / 12 * 3),
    col-4: calc(100% / 12 * 4),
    col-5: calc(100% / 12 * 5),
    col-6: calc(100% / 12 * 6),
    col-7: calc(100% / 12 * 7),
    col-8: calc(100% / 12 * 8),
    col-9: calc(100% / 12 * 9),
    col-10: calc(100% / 12 * 10),
    col-11: calc(100% / 12 * 11),
    col-12: calc(100% / 12 * 12),
);

$grd-container: (
    full: 100%,
); 

$gap:( 
    0: 0rem,            // 0px
    1: 0.0625rem,       // 1px
    2: 0.125rem,        // 2px
    4: 0.25rem,         // 4px 
    8: 0.5rem,          // 8px 
    12: 0.75rem,        // 12px 
    16: 1rem,           // 16px
    20: 1.25rem,        // 20px
    24: 1.5rem,         // 24px
    28: 1.75rem,        // 28px
    32: 2rem,           // 32px
);

$grid-columns:(
    grid-col-1: 1,
    grid-col-2: 2,
    grid-col-3: 3,
    grid-col-4: 4,
    grid-col-5: 5,
    grid-col-6: 6,
    grid-col-7: 7,
    grid-col-8: 8,
    grid-col-9: 9,
    grid-col-10: 10,
    grid-col-11: 11,
    grid-col-12: 12
);

$grid-rows:(
    grid-row-1: 1,
    grid-row-2: 2,
    grid-row-3: 3,
    grid-row-4: 4,
    grid-row-5: 5,
    grid-row-6: 6,
    grid-row-7: 7,
    grid-row-8: 8,
    grid-row-9: 9,
    grid-row-10: 10,
    grid-row-11: 11,
    grid-row-12: 12
);

