$grid-auto-flow:(
    grid-flow-row: row,
    grid-flow-col: column,
    grid-flow-dense: dense,
    grid-flow-row-dense: row dense,
    grid-flow-col-dense: column dense
);

$grid-auto-columns:(
    grid-auto-col-auto: auto,
    grid-auto-col-min: min-content,
    grid-auto-col-max: max-content,
    grid-auto-col-fr: 1fr
);

$grid-auto-rows:(
    grid-auto-row-auto: auto,
    grid-auto-row-min: min-content,
    grid-auto-row-max: max-content,
    grid-auto-row-fr: 1fr
);

$grid-justify-items:(
    grid-justify-start: start,
    grid-justify-end: end,
    grid-justify-center: center,
    grid-justify-stretch: stretch
);