$accent-color: (
    ac-brand: var(--brand),
    ac-secondary: var(--brand-secondary),
    ac-active: var(--active),
    ac-disabled: var(--disabled),
    ac-red: var(--red),
    ac-green: var(--green),
    ac-white: var(--white),
    ac-black: var(--black),
);

$appearance: (
    appearance-none: none,
    appearance-auto: auto
);

$color-scheme: (
    cs-n: 'normal',
    cs-l:'light',
    cs-d:'dark',
    cs-ol:'only light',
    cs-od:'only dark',
    cs-ld:'light dark',
);

$resize: (
    rs-n: none,
    rs-b: both,
    rs-v: vertical,
    rs-h: horizontal,
);

$user-select: (
    us-n: none,
    us-t: text,
    us-al: all,
    us-a: auto,
);