@use "../config/filter" as filter;
@use "../config/g_prefix" as g;

@each $key, $value in filter.$filter-blur {
    .#{g.$prefix-cls}-#{$key} {
        filter: blur($value);
    }
}

@each $key, $value in filter.$filter-saturate {
    .#{g.$prefix-cls}-#{$key} {
        filter: saturate($value);
    }
}

@each $key, $value in filter.$filter-brightness {
    .#{g.$prefix-cls}-#{$key} {
        filter: brightness($value);
    }
}

@each $key, $value in filter.$filter-contrast {
    .#{g.$prefix-cls}-#{$key} {
        filter: contrast($value);
    }
}

@each $key, $value in filter.$filter-grayscale {
    .#{g.$prefix-cls}-#{$key} {
        filter: grayscale($value);
    }
}

@each $key, $value in filter.$filter-hue-rotate {
    .#{g.$prefix-cls}-#{$key} {
        filter:  hue-rotate(#{$value}deg);
    }
}

@each $key, $value in filter.$filter-invert {
    .#{g.$prefix-cls}-#{$key} {
        filter: invert($value);
    }
}

@each $key, $value in filter.$filter-opacity {
    .#{g.$prefix-cls}-#{$key} {
        filter: opacity($value);
    }
}

@each $key, $value in filter.$filter-sepia {
    .#{g.$prefix-cls}-#{$key} {
        filter: sepia($value);
    }
}