@use "../config/display" as display;
@use "../config/g_prefix" as g;
@use "../config/breakpoints" as bp;
@use "../mixins/function" as fn;

@each $key, $value in display.$display {
    .#{g.$prefix-cls}-#{$key} {
        display: $value;
    }
}

//
///----media classes
@include fn.set-media(
    null,
    null,
    display.$display,
    display,
    bp.$container-breakpoints
);