@use "../config/position" as position;
@use '../config/spacing' as space;
@use '../config/breakpoints' as breakpoints;
@use "../config/g_prefix" as g;

@each $key, $value in position.$position {
    .#{g.$prefix-cls}-#{$key} {
        position: $value;
    }
}


@each $bp, $bp-size in breakpoints.$breakpoints {
    @media #{ $bp-size } {
        @each $key, $value in position.$position {
            .#{g.$prefix-cls}-#{$key} {
                position: $value;
            }
        }
    }
}

@each $key, $value in space.$spacing {
    .#{g.$prefix-cls}-top-#{$key} {
        top: #{$value};
    }

}

@each $key, $value in space.$spacing {

    .#{g.$prefix-cls}-right-#{$key} {
        right: #{$value};
    }

}

@each $key, $value in space.$spacing {
    .#{g.$prefix-cls}-bottom-#{$key} {
        bottom: #{$value};
    }
}

@each $key, $value in space.$spacing {
    .#{g.$prefix-cls}-left-#{$key} {
        left: #{$value};
    }
}